.tabs_titles {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(130px, 1fr));
}
.tabs {
  max-width: 100%;
  overflow: hidden;
}
.tabs_titles .tab_title {
  background: var(--gradient-bg-light);
  padding: 1.5rem;
  text-align: center;
  cursor: pointer;
}

.tabs_titles .tab_title.active {
  transform: scale(1.1);
  transform-origin: center;
  box-shadow: 0 0 12px rgba(0, 0, 0, 0.2);
}

@media screen and (max-width: 992px) {
  .tabs_titles .tab_title.active {
    transform: scale(1.05);
  }
}

.tab_content {
  margin-top: 0.5rem;
}
